<template>
	<div class="dl_item_action">
		<div class="row">
			<div class="col-12 col-sm-9 col-md-10">
				<p>
					{{ input.text }}
				</p>
			</div>
			<div class="col-12 col-sm-3 col-md-2 text-center text-sm-right">
				<button
					@click="send_to_provider"
					type="button"
					class="btn btn-primary btn-send btn-m mt-2 mt-md-0"
				>
					Posla
				</button>
			</div>
		</div>
	</div>
</template>

<script>
	import Data_record from '@/logic/requests/Data_record.js';

	export default {
		props: {
			input: {
				send: String,
				text: String,
			},
		},
		components: {
		},
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			send_to_provider()
			{
				let record_token = this.input.send;
				if (record_token === undefined) {
					console.log('record_token is undefined');
					return;
				}
				this.status = 'sending';
				Data_record.create_data_record(record_token).then(
					(response) => {
						if (response.status === 200) {
							this.status = 'sent';
							return;
						}
						this.error_message = response.result?.message;
						this.status = 'error';
					}
				);
			},
		},
		computed: {
		},
	}
</script>
