<template>
	<div class="dl_title text-center text-sm-left">
		<h5>
			{{ input.heading }}
		</h5>
	</div>
	<div
		:class="['dl_item_file d-flex flex-column flex-md-row align-items-stretch text-center text-md-left', class_status]"
	>
		<div class="dl_image d-flex pb-2 pt-3 p-md-2 mx-auto mx-md-0 mx-auto mx-md-0">
			<div class="img_wrapper">
				<img
					src="@/assets/images/common/dl_img_doc_general.svg"
					width="67" height="67"
					class="my-auto"
				>
			</div>
		</div>
		<div class="dl_filename pt-1 pb-0 py-md-4">
			<p class="my-auto mx-auto mx-md-0">
				{{ filename }}
			</p>
		</div>
		<div class="dl_up_download py-3 py-md-4 px-3">
			<input
				@change="picked_file"
				ref="file_input"
				type="file"
				id="{{ input.tags }}"
			>
			<div class="btn-wrapper my-auto mx-auto mx-md-0">
				<button
					@click="select_file"
					type="button"
					class="btn btn-primary btn-upload btn-m"
				>
					Vyberte sbor
				</button>
				<button
					@click="download_doc"
					:style="{ visibility: (allow_download) ? 'visible' : 'hidden' }"
					type="button"
					class="btn btn-primary btn-download btn-m btn-compact"
				>
					<!-- Stiahnu sbor -->
				</button>
			</div>
		</div>
		<div class="dl_progress">
			<div
				:style="{ height: progress + '%' }"
				class="dl_bar bar_vertical"
			></div>
			<div
				:style="{ width: progress + '%' }"
				class="dl_bar bar_horizontal"
			></div>
		</div>
		<div class="dl_status pt-3 pb-2 py-md-3 py-md-4 pl-md-3">
			<p class="my-auto mx-auto mx-md-0">
				{{ user_info_status }}
			</p>
		</div>
		<div class="dl_action pt-2 pb-3 py-md-3 py-md-4 px-3">
			<button
				@click="send_to_provider"
				:disabled="send_disabled"
				type="button"
				class="btn btn-primary btn-send btn-m my-auto mx-auto mx-md-0"
			>
				Posla
			</button>
		</div>
	</div>
</template>

<script>
	import Get_static_docs from '@/logic/requests/Get_static_docs.js';
	import Data_record from '@/logic/requests/Data_record.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			input: {
				send: String,
				tags: String,
				type: String,
				heading: String,
				image: String,
				state: Object,
			},
		},
		components: {
		},
		emits: ['refresh_session_docs'],
		data() {
			return {
				payload: null,
				status: 'empty', // empty, uploading, uploaded, failed, sending, sent, error,
				filename: '',
				error_message: '',
			}
		},
		created() {
			if (this.input.state === null) {
				this.status = 'empty';
			} else if (typeof this.input.state.dataRecords === 'object' && Object.keys(this.input.state.dataRecords).length > 0) {
				this.filename = this.input.state.name;
				this.status = 'sent';
			} else {
				this.filename = this.input.state.name;
				this.status = 'uploaded';
			}
		},
		methods: {
			select_file()
			{
				this.$refs.file_input.click();
			},
			picked_file(event)
			{
				this.status = 'uploading';
				let file = event.target.files[0];
				if (file.size > 8000000 /*8mb*/) {
					console.log('file size too big: ' + file.size + ', allowed: 8mb');
					this.status = 'failed';
					return;
				}
				this.filename = Functions.slugify_but_keep_file_extension(file.name);
				let formdata = new FormData();
				formdata.append('file', file, this.filename);
				formdata.append('title', this.filename);
				formdata.append('description', 'uploaded by docs landing page');
				formdata.append('type', file.type);
				formdata.append('tags', this.input.tags);
				if (this.input.state !== null) {
					formdata.append('id', this.input.state.id);
				}
				this.payload = formdata;
				// for (let item of this.payload) {
				// 	console.log(item);
				// }
				this.upload_doc();
			},
			upload_doc()
			{
				Get_static_docs.upload_session_document(this.payload).then(
					(response) => {
						if (response.status === 200) {
							this.status = 'uploaded';
							this.$emit('refresh_session_docs');
							return;
						}
						this.status = 'failed';
						console.log(response);
					}
				);
			},
			download_doc()
			{
				let id = this.input.state.id;
				let name = this.input.state.name;
				Get_static_docs.get_session_document(id).then(
					(response) => {
						Functions.downloadBase64(name, response.result.content);
					}
				);
			},
			send_to_provider()
			{
				let record_token = this.input.send;
				if (record_token === undefined) {
					console.log('record_token is undefined');
					return;
				}
				this.status = 'sending';
				Data_record.create_data_record(record_token).then(
					(response) => {
						if (response.status === 200) {
							this.status = 'sent';
							return;
						}
						this.error_message = response.result?.message;
						this.status = 'error';
					}
				);
			},
		},
		computed: {
			send_disabled()
			{
				if (this.status === 'empty' || this.status === 'uploading' || this.status === 'sending') {
					return true;
				}
				return false;
			},
			user_info_status()
			{
				let info = {
					empty: 'Nahrajte prosm prlohu',
					uploading: 'Nahrvam...',
					uploaded: this.filename + ' nahrat',
					failed: 'nahrvanie zlyhalo',
					sending: 'Odosielam...',
					sent: this.filename + ' odoslan do poisovne',
					error: this.error_message,
				};
				return info[this.status];
			},
			class_status()
			{
				let classes = {
					empty: '',
					uploading: '',
					uploaded: 'dl_success',
					failed: 'dl_warning',
					sending: '',
					sent: 'dl_sent',
					error: 'dl_warning',
				};
				return classes[this.status];
			},
			allow_download()
			{
				if (this.status === 'empty' || this.status === 'uploading' || this.status === 'sending') {
					return false;
				}
				return true;
			},
			progress()
			{
				let percentage = {
					empty: 0,
					uploading: 25,
					uploaded: 50,
					failed: 10,
					sending: 80,
					sent: 100,
					error: 65,
				};
				return percentage[this.status];
			},
		},
	}
</script>
