<template>
	<div class="docs_landing_scope">
		<div v-for="(value, key) in template_object" >
			<div
				v-if="Object.keys(value)[0] === 'html_key'"
				v-html="value.html_key"
				class="dl_item_text"
			></div>
			<Send_button
				v-else-if="Object.keys(value)[0] === 'button_key'"
				:input="value.button_key"
			/>
			<Upload_input
				v-else-if="Object.keys(value)[0] === 'input_key' && value.input_key.type === 'single'"
				:input="value.input_key"
				@refresh_session_docs="refresh_session_docs"
			/>
			<div
				v-else-if="Object.keys(value)[0] === 'multiple_key' && value.multiple_key.type === 'multiple'"
				class="row"
			>
				<Multiple
					v-for="(value2, key2) in value.multiple_key.many"
					:input="value2"
					@refresh_session_docs="refresh_session_docs"
				/>
			</div>
		</div>
		<hr>
		<pre v-if="debug" >
			{{ json_result }}
		</pre>
		<Testing_template />
	</div>
</template>

<script>
	import Upload_input from '@/components/docs_landing/Upload_input.vue';
	import Get_form_html from '@/logic/requests/Get_form_html.js';
	import Get_session_docs from '@/logic/requests/Get_session_docs.js';
	import State from '@/store/State.js';
	import Testing_template from '@/components/docs_landing/Testing_template.vue';
	import Send_button from '@/components/docs_landing/Send_button.vue';
	import Multiple from '@/components/docs_landing/Multiple.vue';

	export default {
		props: {
		},
		components: {
			Upload_input,
			Testing_template,
			Send_button,
			Multiple,
		},
		data() {
			return {
				debug: false,
				template: '',
				json_result: '',
				session_docs_state: {},
				clear_data: {},
				debug_template: `
					<h2>Document upload landing page</h2>
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
					</p>
					%% type=single tags=kapota send=dv_send_kapota heading="Kapota auta" %%
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
					</p>
					%% type=single tags=technicak send=dv_send_technicak heading="Technick preukaz" %%
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
					</p>
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
					</p>
					%%
						type=multiple
						%
						tags=kufor1 heading="Kufor 1" image="img-demo-vzor-l.png"
						%
						tags=kufor2 heading="Kufor 2" image="img-demo-vzor-l.png"
						%
						tags=kufor3 heading="Kufor 3" image="img-demo-vzor-l.png"
						%
						tags=kufor4 heading="Kufor 4"
						%
						tags=kufor5 heading="Kufor 5"
					%%
					%% send_button send=dv_send_for_all text="Deus vult dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam" %%
				`,
			}
		},
		created() {
			if (this.we_dont_have_tokens()) {
				return;
			}
			Promise.all(
				[
					this.get_document_template(),
					this.get_session_documents(),
				]
			).then(
				() => {
					this.parse_template_object();
				}
			);
		},
		methods: {
			we_dont_have_tokens()
			{
				if (State.document_page_template() === '' || State.get_session_token() === '') {
					console.log('Session token and template name are required.');
					return true;
				}
				return false;
			},
			get_document_template()
			{
				if (this.debug) {
					return new Promise(
						(resolve, reject) => {
							this.template = this.debug_template;
							resolve();
						}
					);
				} else {
					let docs_template = State.document_page_template();
					return Get_form_html.get_it(docs_template).then(
						(response) => {
							if (response.result.error !== undefined) {
								this.template = response.result.error;
								return;
							}
							this.template = response.result.html;
						}
					);
				}
			},
			get_session_documents()
			{
				return Get_session_docs.session_documents(true).then(
					(response) => {
						this.json_result = JSON.stringify(response, null, 2);
						this.session_docs_state = response;
					}
				);
			},
			refresh_session_docs()
			{
				Promise.all(
					[
						this.get_session_documents(),
					]
				).then(
					() => {
						this.parse_template_object();
					}
				);
			},
			pick_document_state_by_tags(input_data)
			{
				let docs = this.session_docs_state?.result?.data?.documents;
				if (docs === undefined) {
					return null; // can this even happen?
				}
				var i, len = docs.length;
				for (i = 0; i < len; i++) {
					if (input_data.tags === docs[i].tags?.[0]) {
						return docs[i];
					}
				}
				return null;
			},
			parse_template_object()
			{
				// split template text by %% tag
				let split = this.template.split('%%');
				let clear = [];
				let i, len = split.length;
				for (i = 0; i < len; i++) {
					// occurence is either html or data for input, remove empty lines
					let occurence = split[i].replace(/\n/g, '').replace(/\t/g, '').trim();
					// send button for multiple inputs
					if (occurence.indexOf('send_button') !== -1) {
						let input_data = {
							send: occurence.match(/send=\w+/)?.[0].replace('send=', ''),
							text: occurence.match(/text="(.*?)"/)?.[0].replace('text="', '').replace( '"', '' ),
						};
						// save as button component
						clear.push({ button_key: input_data });
					}
					// html doesnt have ..tags=..
					else if (occurence.match(/type=\w+/) === null) {
						// dont store empty strings between real occurences
						if (occurence === '') {
							continue;
						}
						// save html with key html_key and continue loop
						clear.push({ html_key: occurence });
					}
					// otherwise its input data
					else {
						let type = occurence.match(/type=\w+/)?.[0].replace('type=', '');
						let input_data = {
							tags: '',
							type: type,
							send: '',
							heading: '',
							image: '',
							state: null,
							many: [],
						};
						if (type === 'single') {
							input_data = {
								tags: occurence.match(/tags=\w+/)?.[0].replace('tags=', ''),
								type: type,
								send: occurence.match(/send=\w+/)?.[0].replace('send=', ''),
								heading: occurence.match(/heading="(.*?)"/)?.[0].replace('heading="', '').replace( '"', '' ),
								image: occurence.match(/image="(.*?)"/)?.[0].replace('image="', '').replace( '"', '' ),
								state: null,
								many: [],
							}
							// add state from session request
							input_data.state = this.pick_document_state_by_tags(input_data);
							// save input with key input_key
							clear.push({ input_key: input_data });
						} else if (type === 'multiple') {
							let parts = occurence.split('%');
							let i2, len2 = parts.length;
							for (i2 = 0; i2 < len2; i2++) {
								if (parts[i2].trim() === 'type=multiple') {
									continue;
								}
								let input = {
									tags: parts[i2].match(/tags=\w+/)?.[0].replace('tags=', ''),
									heading: parts[i2].match(/heading="(.*?)"/)?.[0].replace('heading="', '').replace( '"', '' ),
									image: parts[i2].match(/image="(.*?)"/)?.[0].replace('image="', '').replace( '"', '' ),
									state: null,
								};
								// add state from session request
								input.state = this.pick_document_state_by_tags(input);
								// save as many array
								input_data.many.push(input);
							}
							// save input with key multiple_key
							clear.push({ multiple_key: input_data });
						} else {
							console.log('Type parameter was undefined.');
						}
					}
				}
				console.log(clear);
				this.clear_data = clear;
			},
		},
		computed: {
			template_object()
			{
				return this.clear_data;
			},
		},
	}
</script>
